<template>
  <div>
    <div id="head">
      <div id="search_area">
        <form role="search">
          <label for="search_box">
            <span></span>
            <input id="search_box" type="text" placeholder=""  v-model="searchKw" v-on:keyup.enter="isSearch()">
            <button id="search_btn" v-on:click.prevent="isSearch()"></button>
          </label>
          <span ref="msg" id="msg">{{ msg }}</span>
        </form>
      </div><!--search_area end-->

      <div id="page_area">
        <input id="page_box" v-bind:value="selectedPage" v-on:input="selectedPage = $event.target.value" v-on:keyup.enter="toSelectedPage" />
        <span></span>
        <span>{{totalPage}}</span>
        <!--<button id="to_page" aria-label="" v-on:click.prevent="toSelectedPage"></button>-->
      </div><!--file_name end-->
    </div><!--"head end-->

    <div id="wrap" v-bind:class="{result_opn:sideFlg==='result'}">
      <div id="side">
        <div id="tmb_area">
          <dl>
            <div v-on:click="toPage(index)"
                 v-on:keyup.enter="toPage(index)"
                 v-for="(item, index) in tileSourcesArray" v-bind:key="index"
                 v-bind:class="{ current_img: './data/' + currentImg === item.url }"
                 tabindex="0"
                 role="button"
                 v-bind:id="'p'+(index+1)"
                 >
              <dt>
                <!--<img v-bind:src="item.url" v-bind:alt="index+1+''">-->
                <img v-lazy="{ src:item.url, loading:'./images/loading.png'}" v-bind:alt="index+1+''">
              </dt>
              <dd>{{index+1}}</dd>
            </div>
          </dl><div id="test"></div>
        </div><!--tmb_area end-->

        <div id="result_area"><div id="txt0"></div>
          <div v-for="(item, index) in filteredData" v-bind:key="index" v-bind:id="'txt'+(index+1)">
            <div v-for="(str, idx) in item" v-bind:key="idx">
              <span
               v-bind:class="{selected:selectedFlg===index+'_'+idx}"
               v-on:click="toPage(index,str.$.X,str.$.Y,str.$.WIDTH,str.$.HEIGHT,idx)"
               v-on:keyup.enter="toPage(index,str.$.X,str.$.Y,str.$.WIDTH,str.$.HEIGHT,idx)"
               role="button"
               tabIndex="0"
               >
               {{index+1}}{{ str.$.STRING }}
              </span>
            </div>
          </div>
        </div><!--result_area end-->
      </div><!--side-->

      <div id="viewer-image" ref="image"></div>

      <button id="left_arrow" aria-label="" v-on:click="toNextPage()" v-on:keyup.enter="toNextPage()" v-bind:disabled="inactive=='last'"></button>
      <button id="right_arrow" aria-label="" v-on:click="toPreviousPage()" v-on:keyup.enter="toPreviousPage()" v-bind:disabled="inactive=='first'"></button>



    </div><!--wrap-->

    <div id="foot">
      <div id="operation_buttons">
        <button id="next" v-on:click="getPage(); execRemoveOverlay();" aria-label=""></button>
        <button id="previous" v-on:click="getPage();  execRemoveOverlay();" aria-label=""></button>
        <button id="fullscreen" aria-label=""></button>
        <button id="reset" aria-label="" v-on:click="isReset()" v-on:keyup.enter="isReset()"></button>
        <button id="zoom-in" aria-label=""></button>
        <button id="zoom-out" aria-label=""></button>
        <button id="rotate-left" aria-label=""></button>
        <button id="rotate-right" aria-label=""></button>
      </div><!--operation_buttons end-->
    </div><!--foot end-->
  </div>
</template>

<script>
import axios from "axios";
import _ from "lodash";
import xml2js from "xml2js";
import OpenSeadragon from "openseadragon";
window.OpenSeadragon = OpenSeadragon;

export default {
  name: "OsdComponent",
  data: function () {
    return {
      xmlFile: "./xml/samp.xml", //xml
      allData: [],
      filteredData: [], //

      searchKw: "", //

      tileSourcesArray: [], //

      totalPage: "", //PAGE
      currentImg: "",//
      currentPage: "",//
      selectedPage:"",//

      inactive:"first",

      sideFlg:"",//
      selectedFlg:"",//

      msg: "",
    };
  },
  async mounted() {
    console.log("start"); // 
    this.msg="";
    //xml
    await axios.get(this.xmlFile).then((response) => {
      xml2js.parseString(response.data, (err, result) => {
        if (err) {
          console.log(err);
        } else {
          //
          this.allData = result.OCRDATASET.PAGE.flat(0);
          console.log("got"); // 
          this.msg="";

          //
          this.totalPage = result.OCRDATASET.PAGE.length;

          //tileSource
          var img = [];
          for (var i = 0; i < this.totalPage; i++) {
            img.push(this.allData[i].$.IMAGENAME); //
          }
          this.tileSourcesArray = img.map((item, index) => ({
            type: "image",
            url: "./data/" + img[index],
          }));

        }
      });
    });

    this.initViewer();
    this.getPage();
    this.toFocus();
    this.paramsCheck();
  },
  methods: {
    //
    initViewer: function () {
      this.viewer = OpenSeadragon({
        id: "viewer-image",
        prefixUrl: "./images/",
        tileSources: this.tileSourcesArray,
        showRotationControl: true,
        sequenceMode: true,
        showNavigator: true,
        fullPageButton: "fullscreen",
        rotateLeftButton: "rotate-left",
        rotateRightButton: "rotate-right",
        zoomInButton: "zoom-in",
        zoomOutButton: "zoom-out",
        homeButton: "reset",
        nextButton: "next",
        previousButton: "previous",
      });
    },
    //
    isSearch: function (w) {
      var data = this.allData;
      var kw = this.searchKw.trim(); //,
      this.msg="";

      //
      if(w){
        kw=w;
      }

      if (!kw) {
        this.msg = "";
        this.sideFlg="";
      }else{

        //
        kw = kw.replace(/</g, ' ');
        kw = kw.replace(/>/g, ' ');
        kw = kw.replace(/'/g, ' ');
        kw = kw.replace(/&/g, ' ');
        kw = kw.replace(/"/g, ' ');
        kw = kw.replace(/\//g, ' ');
        kw = kw.replace(//g, " ");

        let kwsArray = kw.split(" ");
        kwsArray = kwsArray.filter((string) => {
          return string !== "";
        });

        //
        kwsArray = kwsArray.map((keyword) => {
          return this.laceAllByConvertTable(keyword);
        });
        //
        this.filteredData =_.map(data,(pa) =>{
          return _.filter(pa.LINE ,(ch)=>{
            return _.every(kwsArray, keyword => _.includes(this.laceAllByConvertTable(ch.$.STRING), keyword));
          });
        });
        //
        var hitCount=_.flatten(this.filteredData).length;
        if(hitCount===0){
          this.msg = "";
          this.sideFlg="";
        }else{
          this.sideFlg="result";
        }
      }

      //
      this.execRemoveOverlay();
      //
      this.toPage(0);
      //
      let posId = document.getElementById('txt0');
      posId.scrollIntoView({
          behavior: 'smooth',
          block: 'start'
        })
    },
    //
    toPage: function (index,x,y,w,h,idx) {
      this.viewer.goToPage(index);//

      //
      var iw=this.allData[index].$.WIDTH;

      //
      var ox=x/iw;
      var oy=y/iw;
      var ow=w/iw;
      var oh=h/iw;

      this.viewer.removeOverlay("runtime-overlay");

      var elt = document.createElement("div");
      elt.id = "runtime-overlay";
      elt.className = "current-ocr";
      this.viewer.addOverlay({
        element: elt,
        location: new OpenSeadragon.Rect(ox, oy, ow, oh)
      });

      this.selectedFlg=index+'_'+idx;

      this.getPage();
    },
    getPage:function(){
      const idx=this.viewer.currentPage();//
      this.currentPage=idx;

      this.selectedPage=this.currentPage+1;//
      this.currentImg=this.allData[idx].$.IMAGENAME;//

      if (idx === 0) {
        this.inactive = "first";
      } else if (idx === (this.totalPage - 1)) {
        this.inactive = "last";
      } else {
        this.inactive = "";
      }
    },
    toSelectedPage:function(){
      const selectedPage= Number(this.selectedPage.trim()); //,
      const pageMsg="1"+this.totalPage+"";

      if(isNaN(selectedPage)){//
        this.msg=pageMsg;
      }else{//
        if(selectedPage >= 1 && selectedPage <= this.totalPage){
          this.toPage(selectedPage-1);
        }else{//
          this.msg=pageMsg;
        }
      }
    },
    //
    execRemoveOverlay:function(){
      this.selectedFlg="";
      this.viewer.removeOverlay("runtime-overlay");
    },

    //
    toNextPage:function(){
      const p=this.currentPage+1;
      this.toPage(p);
    },
    //
    toPreviousPage:function(){
      const p=this.currentPage-1;
      this.toPage(p);
    },
    //zoomInButton: "zoom-in",
    exeZoomIn:function(){
      this.viewer.zoomInButton(true);
    },
    //
    isReset:function(){
      this.viewer.goToPage(0);//1
      this.searchKw='';//
      this.sideFlg=false;//
      this.msg="";//

      //
      this.execRemoveOverlay();

      this.getPage();
    },
    //
    toFocus:function(){
      //
      const msgFlg=this.msg;
      if(msgFlg){
        this.$refs.msg.focus();
      }
    },
    //
    paramsCheck:function(){
      let initIndex=0;

      let urlParams = new URLSearchParams(window.location.search);

      let pagecode =parseInt(urlParams.get('pagecode'));
      let p =parseInt(urlParams.get('p'));

      let word =urlParams.get('word')

      //
      if((!pagecode)&&(!p)){
        initIndex=0;
      }else{
        if(p){
          initIndex=p;
        }else if(pagecode){
          initIndex=pagecode;
        }

        initIndex--;
        if(!(initIndex >= 0 && initIndex <= (this.totalPage - 1))){
          initIndex=0;
        }
      }
      //
      if(word){
        this.isSearch(word);
        this.searchKw=word;
      }
      //
      if(p){
        //
        this.moveTo('p'+(initIndex+1))
      }
      //
      if(this.sideFlg){
        this.moveTo('txt'+(initIndex+1))
      }
      //
      this.toPage(initIndex);
    },
    moveTo:function(pos){
      window.onload = () => {
        let posId = document.getElementById(pos);
        posId.scrollIntoView({
          behavior: 'smooth',
          block: 'start'
        })
      }
